@charset "UTF-8";
/* Font Faces
–––––––––––––––––––––––––––––––––––––––––––––––––– */
@font-face {
  font-family: "Inter";
  font-style: "normal";
  font-weight: 400;
  src: local("Inter"), url("/fonts/inter-v12-latin-regular.woff2") format("woff2"), url("/fonts/inter-v12-latin-regular.woff") format("woff"), url("/fonts/inter-v12-latin-regular.ttf") format("truetype");
}
@font-face {
  font-family: "Inter";
  font-style: "normal";
  font-weight: 200;
  src: local("Inter"), url("/fonts/inter-v12-latin-200.woff2") format("woff2"), url("/fonts/inter-v12-latin-200.woff") format("woff"), url("/fonts/inter-v12-latin-200.ttf") format("truetype");
}
@font-face {
  font-family: "Inter";
  font-style: "normal";
  font-weight: 700;
  src: local("Inter"), url("/fonts/inter-v12-latin-700.woff2") format("woff2"), url("/fonts/inter-v12-latin-700.woff") format("woff"), url("/fonts/inter-v12-latin-700.ttf") format("truetype");
}
@font-face {
  font-family: "Inter";
  font-style: "normal";
  font-weight: 900;
  src: local("Inter"), url("/fonts/inter-v12-latin-900.woff2") format("woff2"), url("/fonts/inter-v12-latin-900.woff") format("woff"), url("/fonts/inter-v12-latin-900.ttf") format("truetype");
}
/* Reset
–––––––––––––––––––––––––––––––––––––––––––––––––– */
a, button {
  cursor: revert;
}

ol, ul, menu {
  list-style: none;
}

img {
  max-width: 100%;
}

table {
  border-collapse: collapse;
}

input, textarea {
  -webkit-user-select: auto;
}

textarea {
  white-space: revert;
}

:where([hidden]) {
  display: none;
}

/* Base
–––––––––––––––––––––––––––––––––––––––––––––––––– */
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 1rem;
}

/* Typography
–––––––––––––––––––––––––––––––––––––––––––––––––– */
h6, .h6, h5, .h5, h4, .h4, h3, .h3, h2, .h2, h1, .h1, p, li, th, td, dl, dd, ol, ul {
  margin-bottom: inherit;
  line-height: inherit;
}

th, .th, caption, .caption, h6, .h6, h5, .h5, h4, .h4, h3, .h3, h2, .h2, h1, .h1 {
  font-family: "Inter", sans-serif;
  letter-spacing: -0.75pt;
}

p, li, th, td, dl, dd {
  font-size: 0.875rem;
  margin-bottom: inherit;
  font-weight: 200;
}

h1, .h1 {
  font-size: 2rem;
}

h2, .h2 {
  font-size: 1.5rem;
}

h3, .h3 {
  font-size: 1.17rem;
}

h4, .h4 {
  font-size: 1rem;
}

h5, .h5 {
  font-size: 0.83rem;
}

h6, .h6 {
  font-size: 0.67rem;
}

table {
  margin-bottom: inherit;
}

small, .small {
  font-size: 0.83em;
  font-weight: 200;
  font-family: inherit;
}

ul[role=list] {
  list-style-type: disc;
  padding-left: 20px;
}

ol[role=list] {
  list-style-type: decimal;
  padding-left: 20px;
}

table, td, th {
  border: 1px solid;
}

td, th {
  padding: 5px 8px;
  vertical-align: top;
}

section {
  display: flex;
  flex-direction: row;
  gap: 40px;
  margin-bottom: 1rem;
}

article {
  max-width: 75ch;
  margin-bottom: 1rem;
}

aside {
  max-width: 60ch;
  margin-bottom: 1rem;
}

/* Layout
–––––––––––––––––––––––––––––––––––––––––––––––––– */
html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  width: 98%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  flex: 1;
}